0 1.6rem !default;\n$spinkit-size: 1.6rem !default;\n\n// Snackbar default\n$woot-snackbar-bg: #323232;\n$woot-snackbar-button: #ffeb3b;\n\n$swift-ease-out-duration: .4s !default;\n$swift-ease-out-function: cubic-bezier(0.37, 0, 0.63, 1) !default;\n$swift-ease-out: all $swift-ease-out-duration $swift-ease-out-function !default;\n\n// Ionicons\n$ionicons-font-path: '~ionicons/fonts';\n\n// Transitions\n$transition-ease-in: all 0.250s ease-in;\n\n:root {\n  --dashboard-app-tabs-height: 3.9rem;\n}\n","\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n/**\n * All the units used below are pixels due to variable name conflict in widget and dashboard\n **/\n@import '~dashboard/assets/scss/variables';\n@import '~dashboard/assets/scss/mixins';\n\n$space-smaller: 4px;\n$space-small: 8px;\n$space-slab: 12px;\n$space-normal: 16px;\n$space-two: 20px;\n$space-medium: 24px;\n$space-large: 28px;\n$space-larger: 32px;\n\n$font-size-tiny: 12px;\n$font-size-small: 14px;\n$font-size-default: 16px;\n$font-size-medium: 18px;\n\n$color-bg: #ebf0f5;\n\n$border-radius-normal: 5px;\n\n.emoji-dialog {\n  @include elegant-card;\n  background: $color-white;\n  border-radius: $space-small;\n  box-sizing: content-box;\n  height: 300px;\n  position: absolute;\n  right: 0;\n  top: -95px;\n  width: 320px;\n  z-index: 1;\n\n  &::before {\n    @include arrow(bottom, $color-bg, $space-slab);\n    bottom: -$space-slab;\n    position: absolute;\n    right: $space-two;\n  }\n}\n\n.emoji-list--wrap {\n  display: flex;\n  flex-direction: column;\n}\n\n.emoji--item {\n  background: transparent;\n  border: 0;\n  border-radius: $space-smaller;\n  cursor: pointer;\n  font-size: $font-size-medium;\n  height: $space-medium;\n  margin: 0;\n  padding: 0 $space-smaller;\n\n  &:hover {\n    background: var(--s-75);\n  }\n}\n\n.emoji--row {\n  box-sizing: border-box;\n  padding: $space-smaller;\n\n  .emoji--item {\n    height: 26px;\n    line-height: 1.5;\n    margin: $space-smaller;\n    width: 26px;\n  }\n}\n\n.emoji-search--wrap {\n  margin: $space-small;\n  position: sticky;\n  top: $space-small;\n\n  .emoji-search--input {\n    background-color: $color-bg;\n    border: 1px solid transparent;\n    border-radius: $border-radius-normal;\n    font-size: $font-size-small;\n    height: $space-larger;\n    margin: 0;\n    padding: $space-small;\n    width: 100%;\n\n    &:focus {\n      box-shadow: 0 0 0 1px $color-woot, 0 0 2px 3px $color-primary-light;\n    }\n  }\n}\n\n.empty-message {\n  align-items: center;\n  display: flex;\n  flex-direction: column;\n  height: 212px;\n  justify-content: center;\n\n  .emoji-icon {\n    color: var(--s-200);\n    margin-bottom: $space-small;\n  }\n  .empty-message--text {\n    color: var(--s-200);\n    font-size: $font-size-small;\n    font-weight: 500;\n  }\n}\n\n.emoji-item {\n  height: 212px;\n  overflow-y: auto;\n}\n\n.emoji-category--title {\n  color: $color-heading;\n  font-size: $font-size-small;\n  font-weight: 500;\n  line-height: 1.5;\n  margin: 0;\n  padding: $space-smaller $space-small;\n  text-transform: capitalize;\n}\n\n.emoji-dialog--footer {\n  background-color: $color-bg;\n  bottom: 0;\n  padding: 0 $space-smaller;\n  position: sticky;\n\n  ul {\n    display: flex;\n    list-style: none;\n    margin: 0;\n    overflow: auto;\n    padding: $space-smaller 0;\n\n    > li {\n      align-items: center;\n      cursor: pointer;\n      display: flex;\n      justify-content: center;\n      padding: $space-smaller;\n    }\n\n    li .active {\n      background: $color-white;\n    }\n    .emoji--item {\n      align-items: center;\n      display: flex;\n      font-size: $font-size-small;\n\n      &:hover {\n        background: $color-bg;\n      }\n    }\n  }\n}\n","// scss-lint:disable PseudoElement SpaceBeforeBrace VendorPrefix\n$shadow-color-1: rgba(50, 50, 93, 0.08);\n$shadow-color-2: rgba(0, 0, 0, 0.07);\n$shadow-color-3: rgba(50, 50, 93, 0.08);\n$shadow-color-4: rgba(0, 0, 0, 0.05);\n\n$color-shadow-medium: rgba(50, 50, 93, 0.08);\n$color-shadow-light: rgba(50, 50, 93, 0.04);\n$color-shadow-large: rgba(50, 50, 93, 0.25);\n$color-shadow-outline: rgba(66, 153, 225, 0.5);\n\n@mixin normal-shadow {\n  box-shadow: 0 $space-small $space-normal $shadow-color-1,\n    0 $space-smaller $space-slab $shadow-color-2;\n}\n\n@mixin light-shadow {\n  box-shadow: 0 $space-smaller 6px $shadow-color-3, 0 1px 3px $shadow-color-4;\n}\n\n@mixin placeholder {\n  &::-webkit-input-placeholder {\n    @content;\n  }\n\n  &:-moz-placeholder {\n    @content;\n  }\n\n  &::-moz-placeholder {\n    @content;\n  }\n\n  &:-ms-input-placeholder {\n    @content;\n  }\n}\n\n@mixin shadow {\n  box-shadow: 0 1px 10px 2px $color-shadow-medium,\n    0 1px 5px 1px $color-shadow-light;\n}\n\n@mixin shadow-medium {\n  box-shadow: 0 4px 24px 4px $color-shadow-medium,\n    0 2px 16px 2px $color-shadow-light;\n}\n\n@mixin shadow-large {\n  box-shadow: 0 10px 15px -16px $color-shadow-medium,\n    0 4px 6px -8px $color-shadow-light;\n}\n\n@mixin shadow-big {\n  box-shadow: 0 20px 25px -20px $color-shadow-medium,\n    0 10px 10px -10px $color-shadow-light;\n}\n\n@mixin shadow-mega {\n  box-shadow: 0 25px 50px -12px $color-shadow-big;\n}\n\n@mixin shadow-inner {\n  box-shadow: inset 0 2px 4px 0 $color-shadow-light;\n}\n\n@mixin shadow-outline {\n  box-shadow: 0 0 0 3px $color-shadow-outline;\n}\n\n@mixin shadow-none {\n  box-shadow: none;\n}\n\n@mixin button-size {\n  min-height: $space-large;\n  min-width: $space-large;\n}\n","@import '~dashboard/assets/scss/variables';\n@import '~widget/assets/scss/mixins';\n\n$spinner-before-border-color: rgba(255, 255, 255, 0.7);\n\n//borders\n@mixin border-nil() {\n  border-color: transparent;\n  border: 0;\n}\n\n@mixin thin-border($color) {\n  border: 1px solid $color;\n}\n\n@mixin custom-border-bottom($size, $color) {\n  border-bottom: $size solid $color;\n}\n\n@mixin custom-border-top($size, $color) {\n  border-top: $size solid $color;\n}\n\n@mixin border-normal() {\n  border: 1px solid $color-border;\n}\n\n@mixin border-normal-left() {\n  border-left: 1px solid $color-border;\n}\n\n@mixin border-normal-top() {\n  border-top: 1px solid $color-border;\n}\n\n@mixin border-normal-right() {\n  border-right: 1px solid $color-border;\n}\n\n@mixin border-normal-bottom() {\n  border-bottom: 1px solid $color-border;\n}\n\n@mixin border-light() {\n  border: 1px solid $color-border-light;\n}\n\n@mixin border-light-left() {\n  border-left: 1px solid $color-border-light;\n}\n\n@mixin border-light-top() {\n  border-top: 1px solid $color-border-light;\n}\n\n@mixin border-light-right() {\n  border-right: 1px solid $color-border-light;\n}\n\n@mixin border-light-bottom() {\n  border-bottom: 1px solid $color-border-light;\n}\n\n// background\n@mixin background-gray() {\n  background: $color-background;\n}\n\n@mixin background-light() {\n  background: $color-background-light;\n}\n\n@mixin background-white() {\n  background: $color-white;\n}\n\n// input form\n@mixin ghost-input() {\n  box-shadow: none;\n  border-color: transparent;\n\n  &:active,\n  &:hover,\n  &:focus {\n    border-color: transparent;\n    box-shadow: none;\n  }\n}\n\n// flex-layout\n@mixin space-between() {\n  display: flex;\n  justify-content: space-between;\n}\n\n@mixin space-between-column() {\n  @include space-between;\n  flex-direction: column;\n}\n\n@mixin space-between-row() {\n  @include space-between;\n  flex-direction: row;\n}\n\n@mixin flex-shrink() {\n  flex: 0 0 auto;\n  max-width: 100%;\n}\n\n@mixin flex-weight($value) {\n  // Grab flex-grow for older browsers.\n  $flex-grow: nth($value, 1);\n\n  // 2009\n  @include prefixer(box-flex, $flex-grow, webkit moz spec);\n\n  // 2011 (IE 10), 2012\n  @include prefixer(flex, $value, webkit moz ms spec);\n}\n\n// full height\n@mixin full-height() {\n  height: 100%;\n}\n\n@mixin round-corner() {\n  border-radius: 1000px;\n}\n\n@mixin scroll-on-hover() {\n  overflow: hidden;\n\n  &:hover {\n    overflow-y: auto;\n  }\n}\n\n\n@mixin horizontal-scroll() {\n  overflow-y: auto;\n}\n\n@mixin elegant-card() {\n  @include normal-shadow;\n  border-radius: $space-small;\n}\n\n@mixin color-spinner() {\n  @keyframes spinner {\n    to {\n      transform: rotate(360deg);\n    }\n  }\n\n  &::before {\n    animation: spinner .9s linear infinite;\n    border: 2px solid $spinner-before-border-color;\n    border-radius: 50%;\n    border-top-color: lighten($color-woot, 10%);\n    box-sizing: border-box;\n    content: '';\n    height: $space-medium;\n    left: 50%;\n    margin-left: -$space-one;\n    margin-top: -$space-one;\n    position: absolute;\n    top: 50%;\n    width: $space-medium;\n  }\n}\n\n// --------------------------------------------------------\n// arrows\n// --------------------------------------------------------\n// $direction: top, left, right, bottom, top-left, top-right, bottom-left, bottom-right\n// $color: hex, rgb or rbga\n// $size: px or em\n// @example\n// .element{\n//     @include arrow(top, #000, 50px);\n// }\n@mixin arrow($direction, $color, $size) {\n  display: block;\n  height: 0;\n  width: 0;\n  content: '';\n\n  @if $direction == 'top' {\n    border-bottom: $size solid $color;\n    border-left: $size solid transparent;\n    border-right: $size solid transparent;\n  }\n\n  @else if $direction == 'right' {\n    border-bottom: $size solid transparent;\n    border-left: $size solid $color;\n    border-top: $size solid transparent;\n  }\n\n  @else if $direction == 'bottom' {\n    border-left: $size solid transparent;\n    border-right: $size solid transparent;\n    border-top: $size solid $color;\n  }\n\n  @else if $direction == 'left' {\n    border-bottom: $size solid transparent;\n    border-right: $size solid $color;\n    border-top: $size solid transparent;\n  }\n\n  @else if $direction == 'top-left' {\n    border-right: $size solid transparent;\n    border-top: $size solid $color;\n  }\n\n  @else if $direction == 'top-right' {\n    border-left: $size solid transparent;\n    border-top: $size solid $color;\n  }\n\n  @else if $direction == 'bottom-left' {\n    border-bottom: $size solid $color;\n    border-right: $size solid transparent;\n  }\n\n  @else if $direction == 'bottom-right' {\n    border-bottom: $size solid $color;\n    border-left: $size solid transparent;\n  }\n}\n\n@mixin text-ellipsis {\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n}\n\n@mixin three-column-grid($column-one-width: 25.6rem,\n  $column-three-width: 25.6rem) {\n  width: 100%;\n  height: 100%;\n  display: grid;\n  grid-template-columns: minmax($column-one-width, 6fr) 10fr minmax($column-three-width, 6fr);\n}\n"]}]);
